.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

@media (max-width: 600px) {
  nav {
    flex-direction: column;
    align-items: center;
  }
}

@keyframes fadeZoomIn {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

section {
  animation: fadeZoomIn 1s ease forwards;
  text-align: center;
  padding: 3rem 1rem;
  background: #fefefe;
}
h1 {
  font-size: 2.8rem;
  margin-bottom: 0.3rem;
  color: #1a1a1a;
}
h2 {
  font-weight: 500;
  color: #555;
  margin-bottom: 1.5rem;
}
a {
  color: #0073b1; /* LinkedIn blue */
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

section {
  animation: slideInLeft 1s ease forwards;
  max-width: 700px;
  margin: 2rem auto;
  padding: 1rem;
  line-height: 1.6;
  color: #333;
  background: #fafafa;
  border-radius: 8px;
  box-shadow: 0 3px 6px rgb(0 0 0 / 0.1);
}
h2 {
  margin-bottom: 1rem;
  color: #222;
}
p + p {
  margin-top: 1rem;
}
@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

section {
  animation: slideInRight 1s ease forwards;
  max-width: 700px;
  margin: 2rem auto;
  padding: 1rem;
  background: #fff;
  border-radius: 6px;
  box-shadow: 0 3px 6px rgb(0 0 0 / 0.1);
  color: #222;
  line-height: 1.5;
}
h2 {
  margin-bottom: 1rem;
  color: #444;
}
p + p {
  margin-top: 1rem;
}
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

section {
  animation: fadeUp 1s ease forwards;
  max-width: 800px;
  margin: 2rem auto;
  padding: 1rem 2rem;
  background: #f5f8fa;
  border-radius: 10px;
  color: #222;
}
h2 {
  margin-bottom: 1.5rem;
  font-weight: 700;
}
h3 {
  margin-top: 1.5rem;
  color: #006699;
}
ul {
  margin-top: 0.6rem;
  list-style-type: disc;
  padding-left: 1.2rem;
  color: #444;
}
@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

section {
  animation: bounceIn 0.8s ease forwards;
  max-width: 600px;
  margin: 2rem auto;
  padding: 1rem 1.5rem;
  background: #e0f7fa;
  border-radius: 8px;
  color: #00796b;
  font-weight: 600;
}
h2 {
  margin-bottom: 1rem;
}
ul {
  padding-left: 1.4rem;
}
li {
  margin-bottom: 0.8rem;
}
section {
  max-width: 700px;
  margin: 2rem auto;
  padding: 1rem 1.5rem;
  background: #fff9e6;
  border-radius: 10px;
  color: #444;
}
h2 {
  margin-bottom: 1.2rem;
  color: #b36b00;
}

ul {
  list-style: none;
  padding-left: 0;
}

li {
  margin-bottom: 1rem;
  font-weight: 600;
}

.skill-bar {
  height: 16px;
  background: #eee;
  border-radius: 10px;
  overflow: hidden;
  margin-top: 0.4rem;
  width: 100%;
  max-width: 500px;
}

.skill-level {
  height: 100%;
  background: linear-gradient(90deg, #ffb347, #ffcc33);
  border-radius: 10px 0 0 10px;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  animation-duration: 1.5s;
}

@keyframes fillSkillBar {
  from {
    width: 0;
  }
  to {
    width: var(--skill-level);
  }
}
@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-40px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

section {
  animation: fadeInLeft 1s ease forwards;
  max-width: 700px;
  margin: 2rem auto;
  padding: 1.5rem 2rem;
  background: #fafafa;
  border-radius: 8px;
  color: #333;
}
h2 {
  margin-bottom: 1.2rem;
  color: #1a237e;
}
h3 {
  margin-top: 1rem;
  color: #3949ab;
}
p {
  margin-top: 0.4rem;
  line-height: 1.5;
}
section {
  max-width: 800px;
  margin: 2rem auto;
  padding: 1rem 2rem;
  background: #fff;
  border-radius: 10px;
  color: #222;
}

h2 {
  margin-bottom: 1.5rem;
  color: #004d40;
}

article {
  margin-bottom: 2rem;
  padding: 1.2rem 1.5rem;
  border: 1px solid #004d40;
  border-radius: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

article:hover {
  transform: scale(1.03);
  box-shadow: 0 12px 20px rgba(0, 77, 64, 0.3);
}

h3 {
  margin-bottom: 0.5rem;
  color: #00796b;
}
ul {
  margin-top: 0.4rem;
  list-style-type: square;
  padding-left: 1.2rem;
}
p {
  margin-top: 0.6rem;
}
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(76, 175, 80, 0.7);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 15px 5px rgba(76, 175, 80, 0.5);
  }
}

section {
  animation: pulse 3s infinite ease-in-out;
  max-width: 700px;
  margin: 2rem auto;
  padding: 1.2rem 1.8rem;
  background: #e8f5e9;
  border-radius: 8px;
  color: #2e7d32;
}
h2 {
  margin-bottom: 1rem;
}
h3 {
  margin-top: 1rem;
  color: #1b5e20;
}
p {
  margin-top: 0.3rem;
}
@keyframes popInGlow {
  0% {
    opacity: 0;
    transform: scale(0.8);
    box-shadow: none;
  }
  70% {
    opacity: 1;
    transform: scale(1.05);
    box-shadow: 0 0 12px 5px #03a9f4;
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 8px 3px #0288d1;
  }
}

section {
  animation: popInGlow 1s ease forwards;
  max-width: 600px;
  margin: 2rem auto;
  padding: 1.5rem 2rem;
  background: #e1f5fe;
  border-radius: 10px;
  text-align: center;
  color: #01579b;
}
a {
  color: #0288d1;
  font-weight: 600;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
h2 {
  margin-bottom: 1rem;
}
p {
  margin: 0.6rem 0;
}
body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(270deg, #d9afd9, #97d9e1);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
  color: #222;
}

@keyframes gradientBG {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

section {
  padding: 80px 20px;
  min-height: 100vh;
  scroll-margin-top: 70px;
}
